* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list {
  list-style: none;
  padding: 0;
  margin: 0;
  font-weight: normal;
  font-size: 18px;
}

.country-info {
  
}

.country-item {
  display: flex;
  align-items: center;
}

.country-info .country-name {
  margin: 0;
}

.country-img {
  margin-right: 10px;
}

.info-description {
  font-weight: bold;
}

.description {
  display: flex;
  flex-direction: column;
}

.country-main {
  display: flex;
  align-items: center;
  font-weight: bold;
  font-size: 30px;
  text-transform: uppercase;
}

.info-paragraphs {
  margin: 0;
  font-weight: normal;
  font-size: 20px;
}